// import store from '../service/vuexService'
// import Vue from 'vue'
const poolModuleModule = {
    state: {
        defaultPoolModuleUrl: '/.cmp/config/pool-module.config',
        current: 0
    },
    mutations: {
        ChangePoolModuleUrl(state, payload) {
            state.defaultPoolModuleUrl = payload
        },
        SetPoolModuleCurrent(state, payload) {
            state.current = payload
        }
    },
    // Action  mutation
    // Action 
    actions: {},
    //  store 
    getters: {
        getPoolModuleUrl: state => {
            return state.defaultPoolModuleUrl
        }
    }
}

export default poolModuleModule



